* {
    box-sizing: border-box;
}
body {
    background: #eee;
    margin: 20px 0;
}
a {
    color: #888;
    text-decoration: none;
    border-bottom: 1px solid;
}
p, li {
    color: #666;
}
h3, h4, h5, h6 {
    margin: 0;
}
h4, h5, h6 {
    font-weight: normal;
}
ol, ul {
    list-style: none;
    padding: 0;
    margin: 0;
}
strong {
    color: #f60;
}
@media print {
    body {
        margin: 0;
        background-color: #fff;
    }
    .page2 {
        page-break-before: always;
        margin-top: 40px;
    }
    #skills {
        display: none;
    }
}
article {
    width: 21cm;
    min-height: 29.7cm;
    background: white;
    margin: 0 auto;
    overflow: auto;
    padding: 1em;
}
p {
    margin: 0.3em 0;
}
h1 {
    font-size: 23px;
}
.bio {
    position: relative;
}
.bio img {
    position: absolute;
    top: -17px;
    right: 2px;
}
.projects {

}
.projects p {
    color: #666;
}
.projects > ol {
    list-style-position: inside;
    padding: 0;
}
.projects > ol > li {
    margin-bottom: 24px;
}
.projects > ol > li > header {
    display: flex;
    justify-content: space-between;
}
.projects > ol > li h3 {
    display: inline-block;
}
.openSource {

}
.openSource > h2 {

}
.openSource > ul {

}
.openSource > ul > li {
    margin-bottom: 24px;
}
.openSource header {
    display: flex;
    justify-content: space-between;
}
.openSource h3 {

}
.skills {

}
#skills {
    width: 500px;
    height: 270px;
}
.skills > .wrapper {
    display: flex;
    justify-content: space-between;
}
.skills > .wrapper > :first-child {
    margin-right: 24px;
}
.skills > .wrapper > #skills {

}
.skills > .wrapper > ul {
    color: #666;
}
.skills > .wrapper > ul > li {
    line-height: 1.5;
    margin-bottom: 8px;
}
.jobs {

}
.jobs > h2 {

}
.jobs > ol {

}
.jobs > ol > li {
    margin: 12px 0;
}
.jobs header {
    display: flex;
    color: #666;
    margin-bottom: 12px;
}
.jobs header > h3 {
    font-size: 18px;
    color: #333;
}
.jobs header > h4 {
    margin-left: 1em;
    font-size: 18px;
}
.jobs header > span {
    font-size: 18px;
    margin-left: auto;
}
@media (max-width: 500px) {
    body {
        margin: 0;
    }
    article {
        width: auto;
        height: auto;
    }
    .bio img {
        display: none;
    }
    .skills .wrapper {
        flex-direction: column;
    }
    #skills {
        width: auto;
    }
    .jobs header {
        flex-direction: column;
    }
    .jobs header > h3,
    .jobs header > h4,
    .jobs header > span {
        margin: 0;
    }
}















